<template>
  <el-container>
    <el-header>
      <div class="title">
        <img src="../assets/logo.png" alt="" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="btn">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :style="{ width: widthToggle + 'px' }">
        <div
          class="toggle-button"
          @click="toggle"
          :style="{ width: widthToggle + 'px' }"
        >
          |||
        </div>
        <el-menu
          :default-active="getPath()"
          class="el-menu-vertical-demo"
          :collapse="isCollapse"
          :text-color="'#fff'"
          :active-text-color="'#409eff'"
          :unique-opened="true"
          :router="true"
          :background-color="'#333744'"
        >
          <el-submenu
            background-color="#292c36"
            v-for="item in listData"
            :key="item.path"
            :index="'/' + item.path"
          >
            <template slot="title">
              <i :class="['iconfont', iconObj[item.id]]"></i>
              <span>{{ item.authName }}</span>
            </template>
            <el-menu-item-group v-for="items in item.children" :key="items.id">
              <el-menu-item
                :index="'/' + items.path"
                @click="setPath('/' + items.path)"
                ><i class="el-icon-menu"></i>{{ items.authName }}</el-menu-item
              >
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main><router-view></router-view></el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      isCollapse: false,
      widthToggle: 200,
      iconObj: {
        125: 'icon-user',
        103: 'icon-tijikongjian',
        101: 'icon-shangpin',
        102: 'icon-danju',
        145: 'icon-baobiao'
      },
      listData: []
    }
  },
  methods: {
    setPath (e) {
      sessionStorage.setItem('path', e)
    },
    getPath () {
      return sessionStorage.getItem('path') || '/users'
    },
    btn () {
      this.$confirm('此操作将退出登录, 是否继续?', '温馨提示', {
        confirmButtonText: '再次确认',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 清除token
          localStorage.removeItem('token')
          // localStorage.removeItem('path')
          // 跳转至login 界面
          this.$router.push('/login')
          this.$message({ type: 'success', message: '退出成功!' })
        })
        .catch(() => {
          this.$message({ type: 'info', message: '已取消' })
        })
    },
    toggle () {
      // 切换收放状态
      this.isCollapse = !this.isCollapse
      this.widthToggle = this.widthToggle > 100 ? 64 : 200
    },
    aa (e) {
      console.log(e, 'HOME')
    },
    // 获取左侧菜单权限列表
    async getList () {
      const token = localStorage.getItem('token')
      if (token) {
        const res = await this.$axios.get('/menus')
        // 对 data 中数据赋值
        this.listData = res.data.data
      }
    }
  },
  mounted () {
    this.getList()
  }
  /* activated () {
    this.$router.push('/users')
  },
  deactivated () {
    localStorage.removeItem('token')
  } */
}
</script>

<style lang="less" scoped>
.el-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 0;
  height: 60px;
  background-color: #373d41;
  .title {
    height: 60px;
    display: flex;
    align-items: center;
    img {
      width: 60px;
      height: 60px;
    }
    span {
      font-size: 20px;
      color: #fff;
      margin-left: 15px;
    }
  }
  .el-button {
    width: 70px;
    height: 40px;
    color: #fff;
  }
}
.el-aside {
  height: 100%;
  &::-webkit-scrollbar {
    width: 0;
  }
  .toggle-button {
    width: 100%;
    height: 24px;
    line-height: 24px;
    font-size: 12px;
    text-align: center;
    color: #fff;
    background-color: #4a5064;
  }
  .el-menu {
    transition: none;
    border: 0;
    height: 100%;
    .el-submenu {
      i {
        margin-right: 10px;
      }
      span {
        font-size: 14px;
      }
      /deep/.el-submenu__title {
        &:hover {
          background-color: #292c36;
        }
      }
      .el-menu-item {
        font-size: 14px;
        &:hover {
          background-color: #292c36;
        }
      }
      // /deep/.el-menu-item-group__title {
      //   padding: 0;
      // }
    }
  }
}
.el-main {
  height: 100%;
  background-color: #e9eef3;
  &::-webkit-scrollbar {
    width: 0;
  }
}
.el-container {
  height: 100%;
  background: #eaedf1;
}
</style>
